#uiContainer {
  position: absolute;
  left: 50%;
  bottom: 0rem;
  width: 40vw;
  transform: translateX(-50%);
  background-color: #0e0e13;
  border-radius: 20px 20px 0px 0px;
  padding: 1rem 2rem;
  display: flex;
  transition: width 0.2s ease-in-out;
}
#ui {
  display: flex;
  align-items: center;
  width: 100%;
}
#ui button {
  background-color: transparent;
  font-family: "Roboto";
  color: lightcyan;
  border: none;
  font-size: 1.1rem;
  transition: background-color 0.5s;
  border-radius: 1px;
}
#ui button:hover {
  background-color: rgba(119, 136, 153, 0.562);
}
#gridUI {
  position: absolute;
  top: 6rem;
  left: 2rem;
}
#sliders {
  display: flex;
  flex-direction: column;
}
#buttons {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
#runGame {
  display: flex;
  align-items: center;
  justify-content: center;
}
#runGame div {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

@media only screen and (max-width: 1300px) {
  #uiContainer {
    width: 60vw;
  }
}

@media only screen and (max-width: 1000px) {
  #uiContainer {
    width: 70vw;
  }
}
@media only screen and (max-width: 800px) {
  #uiContainer {
    width: 90vw;
  }
}

.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium {
  font-size: 40px;
}
